    *{
        box-sizing: border-box;
        padding: 0;
        margin: 0;
        font-family: 'Libre Baskerville', serif;
    }
    .header{
        width: 100%;
        line-height: 100px;
        background-color: #f5ba13;
        display: flex;
        padding-left: 50px;
        color: white;
        box-shadow: 5px 3px 15px -5px rgba(0, 0, 0, 0);
        margin-bottom: 3%;
    }

    textarea{
        width: 100%;
        line-height: 30px;
        background: transparent;
        outline: none;
        font-size: 1rem;
        margin: 10px 0;
        text-align: center;
    }

    input{
        width: 100%;
        line-height: 30px;
        background: transparent;
        border: none;
        outline: none;
        font-size: 1.5rem;
        margin: 10px 0;
        font-weight: bold;
        text-align: center;
    }

    .footer{
        width: 100%;
        position: absolute;
        bottom: 0;
        height: 0.9rem;
        text-align: center;
        margin-top: 100%;
    }


    form{
        min-height: 50px;
        color: #ecf0f1;
        padding: 15px 10px 5px  10px ;
        box-shadow: 0 8px 6px -6px black;
        border-radius: 10px;
    }

    .main_note{
        background-color:#bac9d6;
        margin: auto;
        width: 50%;
        padding: 10px;
        border-radius: 40px;
        box-shadow: inset 0 0 10px #000000;
    }
    .note{
        padding: 10px;
        text-align: center;
        margin: 10px;
        background-color:#bac9d6;
        width: 300px;
        height: 200px;
        border-radius:20px;
        box-shadow:   inset 0 0 10px #000000;
    }
    #btnRem{
        margin-top: auto;
        margin-left: 80%;
    }

    #btn{
    margin-left: 85%;
    background-color:white;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    box-shadow: 5px 3px 15px -5px rgba(0, 0, 0, 0);
    }
    #btn:hover{
        background-color: #f5ba13;
    
    }

    .icon{
        color:#f5ba13;
    }
    #textarea{
    
        background-color: white;
        border: none;
        border-radius: 20px;
        margin-top: 2%;
    }
    #input{
        background-color: white;
        border: none;
        border-radius: 20px;
    }


.searchbar{
    width: 50%;
    height: auto;
    margin: 0 auto;
    text-align: center;


}

.searchbar input{
    margin: 30px 0;
padding: 5px 10px;
border-radius: 5px ;
background-color: aqua;

}













